
export default {
  name: "TradingViewChart",
  props: ["symbol", "id", "autosize"],
  computed: {
    theme: function () {
      return this.$vuetify.theme.dark ? "light" : "dark";
    },
  },
  watch: {
    theme: function (newVal) {
      this.createWidget();
    },
  },
  methods: {
    createWidget() {
      // Initialize the TradingView widget inside the mounted hook
      // console.log("widget symbol", `BSE:${this.symbol}`);
      const widgetOptions = {
        symbol: `${this.symbol}`,
        container_id: this.id,
        theme: this.theme,
        interval: "D",
        style: "1",
        show_popup_button: true,
        hide_side_toolbar: false,
        details: true,
        // Add more widget options here
      };

      if (this.autosize) {
        widgetOptions.autosize = true;
      } else {
        widgetOptions.width = 1000;
        widgetOptions.height = 1000;
      }

      try{
        let a = new TradingView.widget(widgetOptions);
      }catch(error){
        console.log(error.response);
      }
      // console.log(a);
    },
  },
  mounted() {
    this.createWidget();
  },
};
